import { ENTER, COMMA } from '@angular/cdk/keycodes';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NoteMModule } from '../note-m/note-m.module';
import { NoteModule } from '../note.module';
import { NotesSerService } from '../notes-ser.service';
interface tag {
  name:string

}
export interface Fruit {
  name: string;
}

@Component({
  selector: 'app-add-note',
  templateUrl: './add-note.component.html',
  styleUrls: ['./add-note.component.scss']
})

export class AddNoteComponent implements OnInit {
  tags:tag[]=[];
  addTags:boolean= false;
  tagsd:tag[]=[];
expended=true;
AddNote:FormGroup;
notes:NoteModule[];
number;
editMode=false;
id:number= -1;
note:NoteMModule;
noteAdd:NoteMModule;
addOnBlur = true;
@ViewChild('note',{static:false}) desctipe;

  readonly separatorKeysCodes = [ENTER, COMMA] as const;
  fruits: Fruit[] = [{name: 'Lemon'}, {name: 'Lime'}, {name: 'Apple'}];
  constructor(private noteS:NotesSerService , private route:Router,private ActiveRouter:ActivatedRoute) {
  
   }

  ngOnInit(){
    this.AddNote= new FormGroup({
      'description':new FormControl('',Validators.required),
      'title':new FormControl('',Validators.required),

    });
this.noteS.taskChange.subscribe(change=>{
  this.notes=change
})
this.notes=this.noteS.get()

  }
  
  open(e){
    console.log(e.path[8]);


    e.path[4].classList.toggle('col-md-12')
    this.expended=!this.expended;
  }
  show(e){
    e.path[4].classList.toggle('col-md-12')
    this.expended=!this.expended;

  }

  edit(i:number){
    if(i != -1){
      this.id =i;
      this.editMode = true;
      this.note = this.noteS.getByIndex(i);
  
      
      this.AddNote= new FormGroup({
        'description':new FormControl(this.editMode?this.note.description:'',Validators.required),
        'title':new FormControl(this.editMode?this.note.title:'',Validators.required),
        'tags' :new FormControl('')
      });
      
    
    }
  }


  formSubmitted(){

    if(this.editMode){
this.noteS.update(this.id,this.AddNote.value);
this.tags = []
this.AddNote.reset();
this.editMode = false;

    }else{
      if(this.AddNote.value=== ''  || !this.AddNote.valid){
        return
      }
   else{
     this.noteS.addNote(this.AddNote.value);
     
     this.tags = []

     this.AddNote.reset()
   
   }
    }
    this.note = this.noteAdd

  
  };

  delete(i){
    this.noteS.delete(i)
  }

CANCEL(){
  this.id = -1
}

addTag(f){
  let value = ''
  if(f.value){
    value= f.value.trim()

  }
  this.tags.push({name:value});
  f.value =''

  
  
}




addN(t){
if(t.value.trim() !==''){


  this.tagsd.push({name:t.value.trim()});

  
  t.value= ''


  this.notes.push(this.tagsd)
}
}

close(i:number){
  this.tagsd.splice(i,1)
}

}
