import { Injectable } from '@angular/core';
import { title } from 'process';
import { Subject } from 'rxjs';
import { NoteMModule } from './note-m/note-m.module';

@Injectable({
  providedIn: 'root'
})
export class NotesSerService {
  private note:NoteMModule[] = [new NoteMModule('ddd','ddd0')];
taskChange = new Subject<NoteMModule[]>();

  constructor() { }

  addNote(note:NoteMModule){
this.note.push(note);
this.taskChange.next(this.note.slice())
  }
  get(){
    return this.note.slice();
  }
  getByIndex(index:number){
    console.log(this.note[index])
    return this.note[index];
  }
  getLength(){
    return this.note.length
  }

  update(index,newOne){
    this.note[index]=newOne;
    this.taskChange.next(this.note.slice())
  }
  delete(index){
    this.note.splice(index,1);
    this.taskChange.next(this.note.slice())
  }
}
 